















































export default {
	data() {
		return {
			logo: '../../static/image/logo_s.png',
			name: '鼎盛润泽',
			wx: '',
			company: '',
			location: '',
			tel: '',
			bank: '',
			account: '',
			tax: '',
			isShow: false
		}
	},
	onLoad(e) {
		uni.showLoading();
		uni.request({
			url: this.$requestUrl+'get_bill_info',
			method: 'GET',
			success: res => {
				let info = res.data.data;
				this.wx = info.wx_code;
				this.company = info.company_name;
				this.location = info.location;
				this.tel = info.tel;
				this.bank = info.bank;
				this.account = info.account;
				this.tax = info.tax;
			},
			fail: () => {},
			complete: () => {
				uni.hideLoading();
			}
		});
	},
	onShareAppMessage() {
		return {
			title: '鼎盛润泽--开票资料',
			path: '/pages/bill/bill'
		}
	},
	methods: {
		makePhoneCall: function(){
			uni.makePhoneCall({
				phoneNumber: this.tel,
			})
		},
		showBillImg: function(){
			this.isShow = true;
		},
		closeBillImg: function(){
			this.isShow = false;
		}
	}
}



